export default {
    name: "GoodsItem",
    props: {
        sort: {
            type: String,
            default: 'tk_total_sales_des'
        },
        type: {
            type: String,
            default: ''
        },
    },
    data() {
        return {
            sortStr: ''
        }
    },
    mounted() {

    },
    beforeDestroy() {

    },
    watch: {
        sort(val) {
            this.sortStr = val
        }
    },
    methods: {
        handleSort(index) {
            switch (index) {
                case 0:
                    this.sortStr = 'tk_total_sales_des';
                    break;
                case 1:
                    this.sortStr = 'tk_total_sales_asc';
                    break;
                case 2:
                    this.sortStr = 'total_sales_des';
                    break;
                case 3:
                    if (this.sortStr == 'price_des') this.sortStr = 'price_asc'
                    else if (this.sortStr == 'price_asc') this.sortStr = 'price_des'
                    else this.sortStr = 'price_asc'
                    break;
            }
            this.$emit('sort', this.sortStr)
        }
    }
}



