export default {
    name: "SearchBox",
    props: {
        showCategory: {
            type: Boolean,
            default: false
        },
        position: {
            type: String,
            default: 'fixed'
        }
    },
    data() {
        return {
            timer: null,
            focus: false,
            keyword: "",
            cateList: [
                {
                    id: 0,
                    label: "全部"
                },
                {
                    id: 1,
                    label: "分类1"
                },
                {
                    id: 2,
                    label: "分类2"
                },
                {
                    id: 3,
                    label: "分类3"
                },
                {
                    id: 4,
                    label: "分类4"
                },
                {
                    id: 5,
                    label: "分类5"
                },
                {
                    id: 6,
                    label: "分类6"
                },
                {
                    id: 7,
                    label: "分类7"
                },
                {
                    id: 8,
                    label: "分类8"
                },
                {
                    id: 9,
                    label: "分类9"
                },
                {
                    id: 10,
                    label: "分类10"
                },
            ],
            activeIndex: 0,
            showItemDetail: false,
        }
    },
    mounted() {

    },
    beforeDestroy() {

    },
    methods: {
        handleKeyWordChange() {
            clearTimeout(this.timer)
            this.timer = setTimeout(() => {
                this.handleSearch()
            }, 500)
        },
        handleSearch() {
            let data = {
                keyword: this.keyword
            }
            this.$emit("search", data)
        },
        handleClearKeyWord() {
            this.keyword = ''
            this.handleSearch()
        },
        handleItemClick(e, item, index) {
            if (this.activeIndex == index) return
            this.activeIndex = index;
            this.$emit('itemClick', {item, index})
        },
        handleItemMore() {
            this.showItemDetail = !this.showItemDetail
            this.$emit('more')
        }
    }
}



